<template>
  <div class="Comment">
    {{blogComment.commentName}}
    <br />
    {{blogComment.commentTime}}
    <br />
    {{blogComment.commentContent}}
    <br />
    <button @click="deleteComment()">删除</button>
    <br />
    <hr />
    <br />
  </div>
</template>
<script>
import req from "../commen/req.js";
export default {
  data() {
    return {};
  },
  methods: {
    deleteComment() {
      let result = req({
        url: "/admin/Blog/deleteComment",
        method: "get",
        params: { commentId: this.blogComment.commentId, id: this.blogId }
      });
      result
        .then(res => {
          console.log(res.data + "成功");
          alert("删除成功");
          location.reload();
        })
        .catch(err => {
          console.log(err + "失败");
          alert("删除失败");
        });

    }
  },
  props: ["blogComment", "blogId"]
};
</script>
<style scoped>
</style>