<template>
  <div class="AddBlog">
    <h2>发布微博</h2>
    <br />标题：
    <input type="text" name="title" id="title" v-model="blogContent.title" />
    <br />
    <br />分类：
    <input v-model="blogContent.classify" type="text" name="classify" id="classify" />
    <br />
    <br />内容：
    <div id="content" ref="content" contenteditable="true"></div>
    <br />
    <input type="file" id="input" ref="input" @change="inputFiles" />
    <br />
    <br />
    <!--  <textarea name="content" id="content" cols="30" rows="10" v-model="blogContent.content"></textarea> -->
    <br />图片：
    <div id="imageContent" ref="imageContent" contenteditable="true"></div>
    <br />
    <input type="file" id="imageInput" ref="imageInput" @change="inputImage" />
    <br />
    <br />
    <br />
    <br />
    <button @click="finish()" type="submit">完成</button>
  </div>
</template>
<script>
import req from "../commen/req.js";
export default {
  data() {
    return {
      blogContent: {
        title: "",
        content: "",
        image: "",
        classify: ""
      }
    };
  },
  methods: {
    inputFiles(event) {
      let s;
      let content = this.$refs.content;
      let img1 = event.target.files[0];
      let reader = new FileReader(); //创建读取文件的方法
      reader.readAsDataURL(img1); //将文件已url的形式读入页面
      reader.onload = function(e) {
        s = reader.result; //把图片的二进制流付给表单ruleForm里面的idImage
        console.log(s);
        let img = `<img src="${s}">`;
      console.log(img);
      content.innerHTML += img;
      };
  //    let s = window.URL.createObjectURL(img1);
    
    },
    inputImage(event) {
      let s;
      let imageContent = this.$refs.imageContent;
      let img1 = event.target.files[0];
      let reader = new FileReader(); //创建读取文件的方法
      reader.readAsDataURL(img1); //将文件已url的形式读入页面
      reader.onload = function(e) {
        s = reader.result; //把图片的二进制流付给表单ruleForm里面的idImage
        console.log(s);
        let img = `<img src="${s}">`;
      console.log(img);
      imageContent.innerHTML += img;
      };
  //    let s = window.URL.createObjectURL(img1);
    
    },
    finish() {
      let content = this.$refs.content;
       this.blogContent.content = content.innerHTML;
          let imageContent = this.$refs.imageContent;
       this.blogContent.image = imageContent.innerHTML;
      console.log(this.blogContent);
      let result = req({
        url: "/admin/Blog/addBlog",
        method: "post",
        data: this.blogContent
      });
      result
        .then(res => {
          console.log("成功" + res.data);
          this.$router.push({ path: "/Home/Blog" });
          location.reload();
        })
        .catch(err => {
          console.log(err);
        });
      /*  this.$http
        .get("http://localhost:8090/admin/Blog/addBlog", {
          params: this.blogContent
        })
        .then(res => {
          console.log("成功" + res.body);
          this.$router.push({ path: "/Home/Blog" });
          location.reload();
        })
        .catch(err => {
          console.log(err);
        });  */
    }
  }
};
</script>
<style scoped>
#content,#imageContent {
  outline: none;
  width: 80%;
  border: 1px solid rgb(0, 0, 0);
  margin-left: 5%;
}
#input,#imageInput {
  margin-left: 5%;
  outline: none;
}

button {
  margin-left: 35%;
  width: 8%;
  height: 5%;
  font-size: 21px;
}
</style>