<template>
  <div ref="Message" class="Message">
    <ul>
      <li v-for="(item,index) in list" :key="index">
        用户名：{{item.name}}
        <br />
        邮箱：{{item.email}}
        <br />
        内容：{{item.content}}
        <br />
        <button :key="index" @click="deleteMessage(index)">删除</button>
        <br />
        <hr />
        <br />
      </li>
    </ul>
  </div>
</template>
<script>
import req from "../commen/req.js";
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    deleteMessage(index) {
      console.log(index);
      console.log(this.list[index].id);
      let result = req({
        url: "/admin/Message/delete",
        method: "get",
        params: { id: this.list[index].id }
      });
      result
        .then(res => {
          console.log(res.data);
          alert("删除成功");
          location.reload();
        })
        .catch(err => {
          console.log(err);
        });
 
    }
  },
  mounted() {
    let result = req({
      url: "/admin/Message",
      method: "get"
    });
    result
      .then(res => {
        console.log(res.data);
        this.list = res.data;
      })
      .catch(err => {
        console.log(err);
      });
   
  }
};
</script>
<style scoped>
.Message {
  margin-left: 3%;
}
</style>