<template>
  <div class="Blog">
    标题:
    <input type="text" name="title" id="title" v-model="blogContent.title" />
    <br />分类:
    <input v-model="blogContent.classify" type="text" name="classify" id="classify" />
    <br />内容：
    <br />
    <textarea v-html="blogContent.content" name="content" id="content" cols="30" rows="10" v-model="blogContent.content"></textarea>
    <br />
    <button @click="finish()" type="submit">完成</button>
  </div>
</template>
<script>
import req from "../commen/req.js";
export default {
  data() {
    return {
      blogContent: {
        id: "",
        title: "",
        content: "",
        classify: ""
      }
    };
  },
  methods: {
    finish() {
      console.log(this.blogContent);
      let result = req({
        url: "/admin/Blog/doChangeBlog",
        methos: "get",
        params: this.blogContent
      });
      result
        .then(res => {
          console.log("成功" + res.body);
          this.$router.push({ path: "/Home/Blog" });
          location.reload();
        })
        .catch(err => {
          console.log(err);
        });
  
    }
  },
  mounted() {
    let result = req({
      url: "/admin/Blog/changeBlog",
      methos: "get",
      params: { id: this.$route.query.id }
    });
    result
      .then(res => {
        console.log(res.data[0]);
        this.blogContent.id = res.data[0].id;
        this.blogContent.title = res.data[0].title;
        this.blogContent.content = res.data[0].content;
        this.blogContent.classify = res.data[0].classify;
      })
      .catch(err => {
        console.log(err);
      });
  
  }
};
</script>
<style scoped>
img {
  width: 30%;
  height: 300px;
}
.Blog {
  background-color: rgb(237, 248, 243);
  position: absolute;
  left: 16px;
}
input {
  margin-left: 2%;
}
button {
  width: 10%;
  height: 7%;
  margin-left: 5%;
}
textarea {
  margin-left: 5%;
}
</style>